body {
  padding: 0%;
}
.maindiv {
  padding: 10%;
}
.App {
  display: grid;
  background-color: whitesmoke;
  font-family: sans-serif;
  text-align: center;
  border: 1% solid #000000;
  border-radius: 10px;
  overflow: hidden;
}
.details {
  grid-row: span 50;
}
.discount {
  opacity: 0.4;
  text-decoration: line-through;
}
.buttondiv {
  grid-row: span 2;
  margin: 6px;
}
.card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.image {
  height: 90px;
  background-color: gray;
  padding-top: 50px;
  margin: 0;
}
.buttoncart {
  padding: 10px;
}
.cartno {
  margin-left: 85%;
  padding: 5px;
  margin-bottom: 2%;
}
@media (min-width: 540px) {
  .card {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 840px) {
  .card {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
